html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

*, *:before, *:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: Raleway;
  overflow-x: hidden;
}

a {
  color: #000000;
  transition: all 0.3s ease-in-out;
  text-decoration: none;
}

a:hover {
  color: #ffffff;
}

strong {
  font-weight: 700;
}

.section {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20%;
  font-size: 2rem;
  line-height: 3rem;
  text-align: center;
  min-height: 100vh;
}

.section:nth-child(even) {
  background-color: black;
  color: #ffffff;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  border: none;
  padding: 1rem 1.5rem;
  border-radius: 3px;
  font-size: calc(0.25rem + 0.75vw);
  background-color: transparent;
  font-family: NewYork;
}

.btn--color {
  background-color: #FF0043;
  color: #ffffff;
}

.main-color {
  color: #FF0043;
  font-style: italic;
}

.header {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  align-items: center;
  padding: 1.5% 5%;
  margin: 0 auto;
  background-color: #FF0043;
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 5rem;
}

.logo__link {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  overflow: hidden;
  height: 3.5vh;
}

.logo__link svg,
.logo__link img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.logo__caption {
  display: flex;
  font-size: calc(0.25rem + 1vw);
  width: 100%;
  letter-spacing: 1.5px;
  color: #FF0043;
  font-weight: 700;
}

.logo__caption--bottom {
  justify-content: flex-end;
  text-align: right;
}

.navigation {
  width: 45%;
  display: flex;
  margin: 0 auto 0 2.5%;
}

.navigation__inner {
  display: flex;
  width: 100%;
  padding: 1rem 0;
  justify-content: space-between;
}

.navigation__item {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.navigation__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 1rem;
  font-size: calc(0.25rem + 1vw);
}

.login-panel {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.login-panel__item:first-child {
  margin-right: 0.25rem;
}

.choose-language {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 7.5%;
}

.choose-language__item {
  display: flex;
  font-size: 1.25rem;
  border: none;
  background-color: transparent;
  position: relative;
  color: #ffffff;
  text-align: center;
  justify-content: center;
  align-items: center;
  padding: 0 0.5rem;
  transition: all 0.3s ease-in-out;
}

.choose-language__item:hover {
  cursor: pointer;
  color: #354a03;
}

.choose-language__item:nth-child(2n) {
  border-left: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
}

@media screen and (max-width: 1366px) {
  .choose-language {
    width: 10%;
  }
}

@media screen and (max-width: 1224px) {
  .choose-language {
    width: 12.5%;
  }
}

@media screen and (max-width: 1024px) {
  .choose-language {
    width: 17.5%;
  }
}

@media screen and (max-width: 768px) {
  .choose-language {
    width: 22.5%;
  }
}

@media screen and (max-width: 768px) {
  .choose-language {
    width: 30%;
  }
  .choose-language__item {
    width: calc(100% / 3);
  }
}

.hero {
  width: 80%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 30% auto;
  grid-gap: 5rem;
}

.hero__title {
  width: 50%;
  grid-column: 1/3;
  font-weight: 700;
  font-size: calc( 7.5rem + 0.5vw);
  padding-top: 5%;
}

.hero--wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.hero__description {
  font-size: calc(0.25rem + 1vw);
  line-height: calc(0.75rem + 1vw);
  text-align: justify;
}

.hero__btn {
  width: 50%;
  margin-top: 15%;
}

.hero__img {
  overflow: hidden;
}

.hero__img img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.footer {
  border-top: 1px solid rgba(255, 0, 67, 0.35);
  width: 100%;
  padding: 5% 0;
}

.footer__inner {
  width: 80%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-navigation {
  display: flex;
  justify-content: space-between;
}

.footer-navigation__item {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 1rem;
}

.footer-navigation__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 0.5rem;
  font-size: calc(0.25rem + 0.75vw);
}

